export default function Banner(props: BannerProps){

    return(
        <Container>
            <Grid 
                mt={!props.isMobile ? 20 : 5} 
                mb={!props.isMobile ? 0 : 5} 
                container 
                spacing={4} 
                justifyContent={"space-between"} 
                alignItems={"center"} 
                flexDirection={!props.isMobile ? 'row' : 'column-reverse'}
            >
                <Grid item md={5} sm={12}>
                    <Box sx={{display:"flex",justifyContent:"start"}}>
                        <Typography
                            component="h1"
                            variant='overline'
                            sx={{
                                background: "#44D482",
                                
                                width:"200px",
                                textAlign:"center",
                                borderRadius:"2px",
                                fontWeight:"bold",
                                marginBottom:"2em"
                            }}
                        > 
                        SOFTWARE ENGINEER 
                        </Typography>
                    </Box>

                    <Typography 
                        component="h2"
                        variant='h4'
                        color="#ffffff"
                        fontWeight={"regular"}
                        marginBottom="1em"
                        >
                        Truth can only be found in one place: the code
                    </Typography>

                    <Typography
                        component="p"
                        variant='body1'
                        color="#ffffff"
                        marginBottom="1em"
                    >
                        I design and code simple things beautifully, <br />and i love what i do.
                    </Typography>

                    <Button 
                        variant="text" 
                        size="large" 
                        color="primary" 
                        href={SocialMedia.MAIL_TO}
                        sx={{
                            textDecoration:"underline",
                            fontWeight:"medium",
                            ":hover": { textDecoration: "underline",},
                            marginBottom:"1em"
                        }}
                        > 
                            Let's Talk 
                    </Button>
                    <Box display="flex" >
                        <KpiCount count={getYearAndMonth(new Date("2019/06/01"))} text='YEARS EXPERIENCE' />
                        <KpiCount count={"9.0"} text='PROJECT COMPLETED ON 1 ORGANIZATION' />
                    </Box>

                </Grid>
                <Grid item md={5} sm={12}>
                    <img src={BannerImage} height={!props.isMobile ? (758 / 1.8) : (758 / 2.5)} width={!props.isMobile ? (703 / 1.8) : (703 / 2.5)} alt='Abhishek-banner'/>
                </Grid>
            </Grid>
        </Container>
    )
}